// Taken from https://www.digitalocean.com/community/tutorials/react-tabs-component
import React, { Component } from 'react';
import PropTypes from 'prop-types';

class Tab extends Component {

    static propTypes = {
        activeTab: PropTypes.string.isRequired,
        label: PropTypes.string.isRequired,
        onClick: PropTypes.func.isRequired,
    };

    onClick = () => {
        if (this.props.label.length > 0) {
            const { label, onClick } = this.props;
            onClick(label);
        }
        if (this.props.label.toLowerCase() === "text") {
            this.props.setTextTabSelected(true);
        } else {
            this.props.setTextTabSelected(false);
        }
    }

    render() {
        const {
            onClick,
            props: {
                activeTab,
                label,
            },
        } = this;

        let className = 'tab-list-item';

        if (activeTab === label) {
            className += ' tab-list-active';
        }

        return (
            <li
                className={className}
                onClick={onClick}
            >
                {label}
            </li>
        );
    }
}

export default Tab;